<template>
	<view class="main" v-if="loading">

		<view class="address-box">
			<navigator class="address" v-if="address_lists.length>0" hover-class="none" url="../address/index">
				<view class="info">
					<view class="qu">
						<view class="tag">默认</view>{{address_lists[0].address_name}}
					</view>
					<view class="area">{{address_lists[0].address_details}}</view>
					<view class="name">{{address_lists[0].realname}}({{address_lists[0].mobile}})</view>
				</view>
				<uni-icons type="arrowright" color="#777"></uni-icons>
			</navigator>
			<navigator v-else class="address-add" hover-class="none" url="../address/index">
				<uni-icons type="plus-filled" color="#777"></uni-icons>
				<view class="txt">请添加收货地址</view>
				<uni-icons type="arrowright" color="#777"></uni-icons>
			</navigator>
		</view>
		<!-- 	<view class="address-box">
			<view class="address-box-title">服务提示</view>
			<view class="address-box-info">师傅上门将有短信及自动语音电话提示.请您注意查看短信及接听自动语言电话</view>
		</view> -->
		<uni-data-picker :localdata="items" popup-title="请选择预约日期" ref="picker" @change="setYuyue">
			<view class="cell-box">
				<view class="cell">
					<view class="label">
						选择上门时间
					</view>
					<view class="right red">
						{{datetime_txt}}
						<uni-icons type="right" color="#F65425"></uni-icons>
					</view>
				</view>
				<view class="tips">
					<uni-icons type="info-filled" color="#999"></uni-icons> 请您注意查看短信及接听自动语言电话！
				</view>
			</view>
		</uni-data-picker>
		<view class="address-box">
			<view class="service-lists">
				<view class="item">
					<image :src="detail.thumb" class="thumb"></image>
					<view class="info">
						<view class="name">{{detail.name}}</view>
						<view class="price">价格：<text>￥</text>{{detail.price}}起</view>
					</view>
				</view>
			</view>
		</view>
		<view class="cell-box">
			<view class="cell">
				<view class="label">服务费用</view>
				<view class="right">
					<text>￥</text>{{detail.price}}起
				</view>
			</view>
		</view>
		<view class="cell-box">
			<view class="cell">
				<view class="label">备注</view>
				<view class="right">
					<uni-easyinput placeholder="请输入您的要求" :inputBorder="false" v-model="remarks"></uni-easyinput>
				</view>
			</view>
		</view>
		<view class="cell-box">
			<view class="cell">
				<view class="label">
					费用说明
				</view>
				<view class="right red">
					
				</view>
			</view>
			<view class="tips">
				这个费用仅仅是上门的费用，不支持退款
			</view>
		</view>
		<view class="submitBar">
			<view class="price">￥{{detail.price}}</view>
			<view class="btn" @click="save">提交订单</view>
		</view>
		<view style="height: 140rpx;"></view>
		<uni-popup ref="popup" type="center">
			<view class="popup">
				<view class="popup-title">支付订单</view>
				<view class="dl">
					<view class="dt">维修地址</view>
					<view class="dd">{{address_lists[0].realname}} {{address_lists[0].mobile}}</view>
					<view class="dd">{{address_lists[0].address_details}}</view>
				</view>
				<view class="dl">
					<view class="dt">预约日期</view>
					<view class="dd">
						{{datetime_txt}}
					</view>
				</view>
				<view class="dl">
					<view class="dt">备注信息</view>
					<view class="dd">{{remarks ? remarks:'暂无'}}</view>
				</view>
				<!-- <view class="info">
					<uni-icons type="info-filled" color="#2F80FF"></uni-icons> 下单后可以修改配送信息
				</view> -->
				<view class="btn" @click="post">支付订单</view>
			</view>
		</uni-popup>

	</view>
</template>

<script>
	const dayjs = require('@/common/dayjs.min.js');
	export default {
		data() {
			return {
				loading: false,
				address_lists: [],
				cates: [],
				foods: [],
				carts: [],
				show: false,
				remarks: '',
				order: {},
				detail: {},
				id: 0,
				datetime: [],
				times: [
					'08:00-09:00', '09:00-10:00', '10:00-11:00', '11:00-12:00', '12:00-13:00', '13:00-14:00',
					'14:00-15:00', '15:00-16:00', '16:00-17:00', '17:00-18:00', '18:00-19:00', '19:00-20:00',
					'20:00-21:00', '21:00-22:00', '22:00-23:00'
				],
				items: []
			}
		},
		onLoad(option) {
			if (this.vuex_login == 0) {
				this.$u.route('pages/login/index');
				return
			}
			this.id = option.id
			this.setTime()
			//this.getCart()
			this.getDetail()
			this.getAddress()
		},
		onShow() {
			uni.$on('selectAddress', (data) => {
				console.log(data)
				this.address_lists[0] = data.address
				this.$forceUpdate()
			})
		},
		computed: {
			total() {
				return this.detail.price
			},
			datetime_txt() {
				if (this.datetime.length > 0) {
					return this.datetime.join(' ')
				}
				return '请选择时间'
			}
		},
		methods: {
			async getDetail() {
				let res = await this.$u.get('/v2/service/show', {
					id: this.id
				})
				this.detail = res.data[0]
				this.loading = true
			},
			setYuyue(e) {
				console.log(this.datetime)
				let date = e.detail.value.map(item => {
					return item.text;
				})
				this.datetime = date
			},
			setTime() {
				let day = new Date()
				//this.datetime = this.$u.timeFormat(time.getTime(), 'yyyy-mm-dd hh:MM:ss')
				let childrens = []
				this.times.forEach((item) => {
					childrens.push({
						text: item,
						value: item
					})
				})
				for (let i = 0; i < 15; i++) {
					let time = dayjs().add(i, 'day').format('YYYY-MM-DD').toString()
					this.items.push({
						text: time,
						value: time,
						children: childrens
					})
				}

			},
			async getAddress() {
				let res = await this.$u.get('/v2/member/address', {
					uid: this.vuex_user.id,
					is_default: 1
				})
				this.address_lists = res.data.data
			},
			async getCart() {
				let res = await this.$u.get('/v2/cart/index', {
					uid: this.vuex_user.id,
					type: 1
				})
				this.carts = res.data.lists

			},
			confirm(e) {
				console.log(e)
				this.delivery = e[0].value
				this.delivery_txt = e[0].label
			},
			radioChange(evt) {
				console.log(evt)
				this.type = Number(evt.detail.value)
				this.$refs.Calendar.reset()
			},
			standChange(evt) {
				this.cateid = evt.detail.value
				this.getFoods()
			},
			result(evt) {
				console.log(evt)
				this.selectTimes = evt
			},
			async save() {
				if (this.address_lists.length == 0) {
					this.$u.toast('配送地址为空')
					return
				}
				if (this.datetime.length==0) {
					this.$u.toast('上门时间为空')
					return
				}
				/*提交订单*/
				uni.showLoading({
					title: '提交中...'
				})
				let params = {
					uid: this.vuex_user.id,
					goodsjson: [],
					address_lists: this.address_lists[0],
					price: this.total,
					remarks: this.remarks,
					num: 1,
					date: this.datetime[0],
					time: this.datetime[1],
					service_id: this.detail.id,
					service_name: this.detail.name,
					service_thumb: this.detail.thumb
				}

				let res = await this.$u.post('/v2/order/add', params)
				uni.hideLoading()
				if (res.code == 200) {
					this.$refs.popup.open()
					this.order = res.data
				} else {
					this.$u.toast(res.message)
				}
			},
			async post() {
				let res = await this.$u.post('/v2/order/pay', {
					order_id: this.order.id,
					uid: this.order.uid
				})
				if (res.code != 200) {
					this.$u.toast(res.message)
					return
				}
				//this.$u.toast(res.message)
				let config = res.data.config
				let that = this
				uni.requestPayment({
					provider: 'wxpay',
					timeStamp: config.timestamp,
					nonceStr: config.nonceStr,
					package: config.package,
					signType: config.signType,
					paySign: config.paySign,
					success: function() {
						uni.navigateTo({
							url: './index'
						})
					},
					fail: function(err) {
						console.log('fail:' + JSON.stringify(err));
						that.$refs.popup.close()
						that.$u.toast('支付失败')
					}
				});
			}

		}
	}
</script>

<style scoped>
	.main {
		display: flex;
		flex-direction: column;
		min-height: 100vh;
		background-color: #F8F8F8;
	}

	.main .bg {
		width: 750rpx;
		height: 750rpx;
		position: absolute;
		top: 0;
		z-index: 1;
	}

	view {
		z-index: 2;
	}

	.main .title {
		color: #000;
		font-size: 32rpx;
		margin: 30rpx 0 0 30rpx;
	}

	.address-box {
		margin: 20rpx 0 0 0;
		position: relative;
		background-color: #fff;
	}

	.address-box .address-box-title {
		color: #2F80FF;
		margin: 30rpx 0 0 30rpx;
		font-size: 32rpx;
	}

	.address-box .address-box-info {
		color: #2F80FF;
		font-size: 24rpx;
		margin: 20rpx 30rpx 30rpx 30rpx;
		display: flex;
		justify-content: space-between;

	}

	.address-box .address-box-info text {
		color: #777
	}

	.address-box .address {
		line-height: 22px;
		padding: 30rpx;
		display: flex;
		justify-content: space-between;
		align-items: center;
	}

	.address-box .address .info {
		flex: 1;
		color: #777;
		font-size: 28rpx;
	}

	.address-box .address .info .qu {
		display: flex;
		align-items: center;
	}

	.address-box .address .info .qu .tag {
		background-color: rgba(246, 84, 37, 0.1);
		border-radius: 8rpx;
		color: #FD6730;
		padding: 0 15rpx;
		height: 44rpx;
		text-align: center;
		line-height: 44rpx;
		font-size: 24rpx;
		margin-right: 10rpx;
	}

	.address-box .address-add {
		padding: 0 10px;
		display: flex;
		justify-content: space-between;
		align-items: center;
		height: 60px;
	}

	.address-box .address-add .txt {
		flex: 1;
		margin-left: 5px;
		font-size: 14px;
		color: #777
	}

	.address-box .address .area {
		font-size: 32rpx;
		font-weight: bold;
		margin: 15rpx 0;
	}

	.address-box .address .name {
		font-size: 28rpx;
	}

	.calendar_tip {
		margin: 0 0 30rpx 30rpx;
		font-size: 12px;
		color: #999
	}

	.service-lists .item {
		display: flex;
		background-color: #fff;
		border-radius: 16rpx;
		margin: 20rpx 30rpx;
		border-bottom: #F8F8F8 1px solid;
	}

	.service-lists .item:last-child {
		border-bottom: none
	}

	.service-lists .item .thumb {
		width: 160rpx;
		height: 160rpx;
		border-radius: 8rpx;
		margin-right: 20rpx;
	}

	.service-lists .item .info {
		height: 160rpx;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		flex: 1;
	}

	.service-lists .item .info .name {
		font-size: 32rpx;
		font-weight: bold;

	}

	.service-lists .item .info .price {
		color: #F65425;
		font-size: 28rpx;
		font-weight: bold;
	}

	.service-lists .item .info .price text {
		font-size: 24rpx;
	}

	.service-lists .item .info .btn {
		margin-top: auto;
		margin-left: auto;
		font-size: 24rpx;
		border-radius: 20rpx;
		border: #eee 1px solid;
		padding: 10rpx 20rpx;
	}

	.heji {
		display: flex;
		justify-content: space-between;
		align-items: center;
		color: #F74437;
		font-size: 16px;
		margin: 0 20rpx 30rpx 20rpx;
	}

	.cell-box {
		margin-top: 20rpx;
	}

	.cell-box .cell {
		display: flex;
		justify-content: space-between;
		align-items: center;
		position: relative;
		background-color: #fff;
		padding: 0 30rpx;
		height: 96rpx;

	}

	.cell-box .cell .label {
		font-size: 30rpx;
		color: #000;
		font-weight: bold;
	}

	.cell-box .cell .right {
		flex: 1;
		margin-left: 30rpx;
		text-align: right;
		color: #000;
		font-weight: bold;
	}

	.cell-box .cell .right.red {
		font-size: 14px;
		color: #F65425;
	}

	.cell-box .tips {
		background-color: #fff;
		padding: 0 0 20rpx 30rpx;
		color: #999999;
		font-size: 24rpx;

	}

	.submitBar {
		height: 120rpx;
		background-color: #fff;
		margin-top: auto;
		display: flex;
		justify-content: space-between;
		align-items: center;
		overflow: hidden;
		position: fixed;
		width: 750rpx;
		bottom: 0;
		box-shadow: -20px 0 10px rgba(0, 0, 0, 0.1);
		z-index: 10;
	}

	.submitBar .price {
		color: #F65425;
		font-size: 34rpx;
		padding-left: 30rpx;
		font-weight: bold;
	}

	.submitBar .btn {
		background: linear-gradient(80deg, #FF4400 0%, #FF6600 100%);
		line-height: 80rpx;
		width: 240rpx;
		text-align: center;
		color: #fff;
		border-radius: 40rpx;
		margin-right: 30rpx;
	}

	.popup {
		background-color: #fff;
		width: 650rpx;
		border-radius: 24rpx;
		display: flex;
		flex-direction: column;
		overflow: hidden;

	}

	.popup .dl {
		margin: 30rpx 30rpx 0 30rpx;
	}

	.popup .dl .dt {
		color: #FF4400;
		font-size: 16px;
		margin-bottom: 20rpx;
	}

	.popup .dl .dd {
		color: #666;
		font-size: 14px;
		margin: 8rpx 0;
	}

	.popup .popup-title {
		text-align: center;
		margin: 30rpx 0;
		font-size: 36rpx;
	}

	.popup .info {
		text-align: right;
		font-size: 12px;
		color: #666;
		margin: 30rpx 30rpx 0 0;
	}

	.popup .btn {
		text-align: center;
		height: 90rpx;
		line-height: 90rpx;
		background: linear-gradient(83deg, #FF4400 0%, #FF6600 100%);
		color: #fff;
		margin: 30rpx;
		border-radius: 16rpx;
	}
</style>
